import React, { useState } from 'react';
import { colors } from '../../tokens/colors';
import { textStyles } from '../../tokens/typography';
import { spacing } from '../../tokens/spacing';
import { radius } from '../../tokens/radius';
import { shadows } from '../../tokens/shadows';
import './ExerciseCard.css';

export interface ExerciseCardProps {
  /**   */
  title: string;
  /**      */
  description?: string;
  /**   (: , , ) */
  type?: 'cardio' | 'strength' | 'stretching' | 'balance';
  /**   () */
  duration?: number;
  /**   */
  calories?: number;
  /**     */
  icon?: React.ReactNode;
  /**   */
  isCompleted?: boolean;
  /**   */
  isFavorite?: boolean;
  /**   ('fill'   100%,   ) */
  width?: 'fill' | (string & {});
  /**    */
  onClick?: () => void;
  /**    */
  onFavoriteToggle?: () => void;
  /**    */
  onCompleteToggle?: () => void;
  /**  CSS  */
  className?: string;
}

const ExerciseCard: React.FC<ExerciseCardProps> = ({
  title,
  description,
  type = 'cardio',
  duration,
  calories,
  icon,
  isCompleted = false,
  isFavorite = false,
  width = '300px',
  onClick,
  onFavoriteToggle,
  onCompleteToggle,
  className = '',
}) => {
  const [isHovered, setIsHovered] = useState(false);

  //     
  const typeConfig = {
    cardio: {
      color: colors.primary.tint.blue[500],
      bgColor: colors.primary.tint.blue[50],
      icon: '',
      label: '',
    },
    strength: {
      color: colors.primary.tint.red[500],
      bgColor: colors.primary.tint.red[50],
      icon: '',
      label: '',
    },
    stretching: {
      color: colors.primary.tint.green[500],
      bgColor: colors.primary.tint.green[50],
      icon: '',
      label: '',
    },
    balance: {
      color: colors.primary.tint.violet[500],
      bgColor: colors.primary.tint.violet[50],
      icon: '',
      label: '',
    },
  };

  const currentTypeConfig = typeConfig[type];

  // width  
  const getWidth = () => {
    if (width === 'fill') {
      return '100%';
    }
    return width;
  };

  const cardStyles: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    padding: spacing.m,
    backgroundColor: isCompleted ? colors.primary.coolGray[50] : colors.semantic.background.primary,
    border: `1px solid ${isHovered ? currentTypeConfig.color : colors.semantic.border.default}`,
    borderRadius: radius.m,
    boxShadow: isHovered ? shadows.m : shadows.s,
    cursor: 'pointer',
    transition: 'all 0.3s ease',
    transform: isHovered ? 'translateY(-2px)' : 'translateY(0)',
    opacity: isCompleted ? 0.7 : 1,
    position: 'relative',
    overflow: 'hidden',
    width: getWidth(),
    minWidth: '300px', //   
  };

  const handleCardClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (onClick) onClick();
  };

  const handleFavoriteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (onFavoriteToggle) onFavoriteToggle();
  };

  const handleCompleteClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (onCompleteToggle) onCompleteToggle();
  };

  return (
    <div
      className={`exercise-card ${className}`}
      style={{ ...cardStyles }}
      onClick={handleCardClick}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/*     */}
      {isCompleted && (
        <div
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            width: '4px',
            height: '100%',
            backgroundColor: colors.semantic.state.success,
          }}
        />
      )}

      {/*    */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          width: '48px',
          height: '48px',
          backgroundColor: currentTypeConfig.bgColor,
          borderRadius: radius.s,
          marginRight: spacing.m,
          fontSize: '20px',
        }}
      >
        {icon || currentTypeConfig.icon}
      </div>

      {/*   */}
      <div style={{ flex: 1, minWidth: 0 }}>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: spacing.xxs }}>
          <h3
            style={{
              ...textStyles.heading4,
              color: isCompleted ? colors.semantic.text.tertiary : colors.semantic.text.primary,
              margin: 0,
              textDecoration: isCompleted ? 'line-through' : 'none',
            }}
          >
            {title}
          </h3>
          <span
            style={{
              ...textStyles.caption,
              color: currentTypeConfig.color,
              backgroundColor: currentTypeConfig.bgColor,
              padding: `${spacing.xxxs} ${spacing.xs}`,
              borderRadius: radius.xs,
              marginLeft: spacing.xs,
              fontSize: '10px',
              fontWeight: 500,
            }}
          >
            {currentTypeConfig.label}
          </span>
        </div>

        {description && (
          <p
            style={{
              ...textStyles.body3,
              color: colors.semantic.text.secondary,
              margin: 0,
              marginBottom: spacing.xs,
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              whiteSpace: 'nowrap',
            }}
          >
            {description}
          </p>
        )}

        {/*   */}
        <div style={{ display: 'flex', gap: spacing.m, alignItems: 'center' }}>
          {duration && (
            <div style={{ display: 'flex', alignItems: 'center', gap: spacing.xxs }}>
              <span style={{ fontSize: '12px' }}></span>
              <span
                style={{
                  ...textStyles.caption,
                  color: colors.semantic.text.tertiary,
                }}
              >
                {duration}
              </span>
            </div>
          )}
          {calories && (
            <div style={{ display: 'flex', alignItems: 'center', gap: spacing.xxs }}>
              <span style={{ fontSize: '12px' }}></span>
              <span
                style={{
                  ...textStyles.caption,
                  color: colors.semantic.text.tertiary,
                }}
              >
                {calories}kcal
              </span>
            </div>
          )}
        </div>
      </div>

      {/*   */}
      <div style={{ display: 'flex', gap: spacing.xs, alignItems: 'center' }}>
        {/*   */}
        <button
          onClick={handleFavoriteClick}
          style={{
            background: 'none',
            border: 'none',
            cursor: 'pointer',
            fontSize: '16px',
            padding: spacing.xxs,
            borderRadius: radius.xs,
            transition: 'all 0.2s ease',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
          title={isFavorite ? ' ' : ' '}
        >
          {isFavorite ? '' : ''}
        </button>

        {/*   */}
        <button
          onClick={handleCompleteClick}
          style={{
            background: 'none',
            border: `1px solid ${isCompleted ? colors.semantic.state.success : colors.semantic.border.default}`,
            cursor: 'pointer',
            padding: spacing.xs,
            borderRadius: radius.xs,
            transition: 'all 0.2s ease',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            backgroundColor: isCompleted ? colors.semantic.state.success : 'transparent',
          }}
          title={isCompleted ? ' ' : ' '}
        >
          <span
            style={{
              fontSize: '12px',
              color: isCompleted
                ? colors.semantic.background.primary
                : colors.semantic.text.tertiary,
            }}
          >
            {isCompleted ? '' : ''}
          </span>
        </button>
      </div>
    </div>
  );
};

export default ExerciseCard;
