import React, { useState, useRef, useEffect } from 'react';
import { Icon } from '../icon/Icon';
import { Font } from '../font/Font';
import { colors } from '../../tokens/colors';
import { fontWeight, textStyles } from '../../tokens';

export interface StepperProps {
  /**   */
  value?: number;
  /**  */
  min?: number;
  /**  */
  max?: number;
  /**      */
  onChange?: (value: number) => void;
  /**   */
  disabled?: boolean;
  /**   */
  width?: 'fill' | (string & {});
  /**   */
  error?: boolean;
  /**   */
  errorMessage?: string;
  /**   */
  focused?: boolean;
  /**      */
  editable?: boolean;
  /**  CSS  */
  className?: string;
  /**   */
  style?: React.CSSProperties;
  /**  (: "", "", "") */
  unit?: string;
  /**   (: 1) */
  step?: number;
  /**     */
  isTime?: boolean;
  /**    ('sec' | 'min' | 'hour') */
  timeBaseUnit?: 'sec' | 'min' | 'hour';
}

//    
const formatTime = (totalValue: number, baseUnit: 'sec' | 'min' | 'hour'): string => {
  let totalSeconds: number;

  // baseUnit   
  if (baseUnit === 'min') {
    totalSeconds = totalValue * 60;
  } else if (baseUnit === 'hour') {
    totalSeconds = totalValue * 3600;
  } else {
    totalSeconds = totalValue;
  }

  // baseUnit     
  if (baseUnit === 'hour') {
    //  :   
    const hours = totalSeconds / 3600;
    if (hours === Math.floor(hours)) {
      return `${Math.floor(hours)}`;
    }

    const wholeHours = Math.floor(hours);
    const remainingMinutes = Math.round((hours - wholeHours) * 60);

    if (wholeHours === 0) {
      return `${remainingMinutes}`;
    }

    if (remainingMinutes === 0) {
      return `${wholeHours}`;
    }

    return `${wholeHours} ${remainingMinutes}`;
  }

  if (baseUnit === 'min') {
    //  :    (  )
    const totalMinutes = Math.round(totalSeconds / 60);

    if (totalMinutes < 60) {
      return `${totalMinutes}`;
    }

    const hours = Math.floor(totalMinutes / 60);
    const minutes = totalMinutes % 60;

    if (minutes === 0) {
      return `${hours}`;
    }

    return `${hours} ${minutes}`;
  }

  //  : , ,   
  if (totalSeconds < 60) {
    return `${totalSeconds}`;
  }

  if (totalSeconds < 3600) {
    const minutes = Math.floor(totalSeconds / 60);
    const seconds = totalSeconds % 60;

    if (seconds === 0) {
      return `${minutes}`;
    }
    return `${minutes} ${seconds}`;
  }

  const hours = Math.floor(totalSeconds / 3600);
  const remainingSeconds = totalSeconds % 3600;
  const minutes = Math.floor(remainingSeconds / 60);
  const seconds = remainingSeconds % 60;

  let result = `${hours}`;

  if (minutes > 0) {
    result += ` ${minutes}`;
  }

  if (seconds > 0) {
    result += ` ${seconds}`;
  }

  return result;
};

const parseTimeString = (timeStr: string, baseUnit: 'sec' | 'min' | 'hour'): number => {
  //   0 
  if (!timeStr.trim()) {
    return 0;
  }

  // "1 30 45" or "90 30" or "45"   
  const hourMatch = timeStr.match(/(\d+)/);
  const minuteMatch = timeStr.match(/(\d+)/);
  const secondMatch = timeStr.match(/(\d+)/);

  let totalSeconds = 0;
  let hasValidInput = false;

  if (hourMatch) {
    const hours = parseInt(hourMatch[1]);
    if (!isNaN(hours)) {
      totalSeconds += hours * 3600;
      hasValidInput = true;
    }
  }

  if (minuteMatch) {
    const minutes = parseInt(minuteMatch[1]);
    if (!isNaN(minutes)) {
      totalSeconds += minutes * 60;
      hasValidInput = true;
    }
  }

  if (secondMatch && baseUnit === 'sec') {
    //  baseUnit 'sec'  
    const seconds = parseInt(secondMatch[1]);
    if (!isNaN(seconds)) {
      totalSeconds += seconds;
      hasValidInput = true;
    }
  }

  //     ( )
  if (!hasValidInput) {
    const numericValue = parseInt(timeStr.trim());
    if (!isNaN(numericValue)) {
      totalSeconds = numericValue;
      // baseUnit   
      if (baseUnit === 'min') {
        totalSeconds = numericValue * 60;
      } else if (baseUnit === 'hour') {
        totalSeconds = numericValue * 3600;
      }
      hasValidInput = true;
    }
  }

  //     
  if (!hasValidInput) {
    throw new Error('Invalid time format');
  }

  // baseUnit    
  if (baseUnit === 'min') {
    return totalSeconds / 60;
  } else if (baseUnit === 'hour') {
    return totalSeconds / 3600;
  }

  return totalSeconds;
};

export const Stepper: React.FC<StepperProps> = ({
  value = 0,
  min = 0,
  max = 100,
  onChange,
  disabled = false,
  width = '335px',
  error = false,
  errorMessage,
  focused = false,
  editable = false,
  className = '',
  style = {},
  unit = '',
  step = 1,
  isTime = false,
  timeBaseUnit = 'min',
}) => {
  const [internalValue, setInternalValue] = useState(value);
  const [isFocused, setIsFocused] = useState(focused);
  const [isEditing, setIsEditing] = useState(false);
  const [editValue, setEditValue] = useState(String(value));
  const inputRef = useRef<HTMLInputElement>(null);

  const currentValue = onChange ? value : internalValue;
  const isMinReached = currentValue <= min;
  const isMaxReached = currentValue >= max;

  useEffect(() => {
    if (!isEditing) {
      if (isTime) {
        setEditValue(formatTime(currentValue, timeBaseUnit));
      } else {
        setEditValue(String(currentValue));
      }
    }
  }, [currentValue, isEditing, isTime, timeBaseUnit]);

  const handleIncrement = () => {
    if (disabled || isMaxReached) return;
    let newValue = currentValue + step;

    // max  
    if (newValue > max) {
      newValue = max;
    }

    if (onChange) {
      onChange(newValue);
    } else {
      setInternalValue(newValue);
    }
  };

  const handleDecrement = () => {
    if (disabled || isMinReached) return;
    let newValue = currentValue - step;

    // min  
    if (newValue < min) {
      newValue = min;
    }

    if (onChange) {
      onChange(newValue);
    } else {
      setInternalValue(newValue);
    }
  };

  const handleValueClick = () => {
    if (editable && !disabled && !error) {
      setIsEditing(true);
      setIsFocused(true);
      if (isTime) {
        setEditValue(formatTime(currentValue, timeBaseUnit));
      } else {
        setEditValue(String(currentValue));
      }
      setTimeout(() => {
        inputRef.current?.focus();
        inputRef.current?.select();
      }, 0);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const inputValue = e.target.value;
    if (isTime) {
      //     (, , , , )
      //      ( baseUnit  )
      if (inputValue === '' || /^[\d\s]*$/.test(inputValue)) {
        setEditValue(inputValue);
      }
    } else {
      //    
      if (inputValue === '' || /^-?\d*$/.test(inputValue)) {
        setEditValue(inputValue);
      }
    }
  };

  const handleInputBlur = () => {
    setIsEditing(false);
    setIsFocused(false);

    let newValue: number;

    if (isTime) {
      //    
      try {
        newValue = parseTimeString(editValue, timeBaseUnit);
      } catch (error) {
        //     
        setEditValue(formatTime(currentValue, timeBaseUnit));
        return;
      }
    } else {
      newValue = parseInt(editValue, 10);

      //         
      if (editValue === '' || isNaN(newValue)) {
        setEditValue(String(currentValue));
        return;
      }
    }

    // min, max   
    if (newValue < min) {
      newValue = min;
    } else if (newValue > max) {
      newValue = max;
    }

    // step   (min/max  )
    if (step && step > 0) {
      const roundedValue = Math.round(newValue / step) * step;

      //        step  
      if (roundedValue < min) {
        newValue = Math.ceil(min / step) * step;
        if (newValue > max) {
          newValue = min;
        }
      } else if (roundedValue > max) {
        newValue = Math.floor(max / step) * step;
        if (newValue < min) {
          newValue = max;
        }
      } else {
        newValue = roundedValue;
      }
    }

    if (isTime) {
      setEditValue(formatTime(newValue, timeBaseUnit));
    } else {
      setEditValue(String(newValue));
    }

    if (onChange) {
      onChange(newValue);
    } else {
      setInternalValue(newValue);
    }
  };

  const handleInputKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      handleInputBlur();
    } else if (e.key === 'Escape') {
      setIsEditing(false);
      setIsFocused(false);
      if (isTime) {
        setEditValue(formatTime(currentValue, timeBaseUnit));
      } else {
        setEditValue(String(currentValue));
      }
    }
  };

  //   
  const displayValue = () => {
    if (isTime) {
      return formatTime(currentValue, timeBaseUnit);
    } else if (unit) {
      return `${currentValue}${unit}`;
    }
    return String(currentValue);
  };

  //  
  const containerStyle: React.CSSProperties = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    gap: error && errorMessage ? '6px' : '16px',
    width: width === 'fill' ? '100%' : width,
    userSelect: 'none',
    ...style,
  };

  const stepperStyle: React.CSSProperties = {
    display: 'flex',
    alignItems: 'center',
    gap: '15px',
    padding: '12px 16px',
    borderRadius: '8px',
    border: `1px solid ${
      error
        ? colors.semantic.state.error
        : isFocused
          ? colors.primary.coolGray[800]
          : colors.primary.coolGray[300]
    }`,
    backgroundColor: disabled ? colors.primary.coolGray[50] : colors.primary.gray.white,
    width: '100%',
    boxSizing: 'border-box',
    transition: 'all 0.2s ease',
    position: 'relative',
    userSelect: 'none',
  };

  const valueContainerStyle: React.CSSProperties = {
    flex: 1,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'relative',
    cursor: editable && !disabled && !error ? 'text' : 'default',
    userSelect: 'none',
  };

  const inputStyle: React.CSSProperties = {
    ...textStyles.heading3,
    background: 'transparent',
    border: 'none',
    outline: 'none',
    fontWeight: fontWeight.semibold,
    textAlign: 'center',
    color: colors.primary.coolGray[800],
    width: '100%',
    padding: 0,
    margin: 0,
    userSelect: 'none',
  };

  return (
    <div className={`stepper ${className}`} style={containerStyle}>
      <div style={stepperStyle}>
        <Icon
          type="minus-circle-filled"
          size={24}
          color={
            disabled || isMinReached ? colors.primary.coolGray[400] : colors.primary.coolGray[800]
          }
          onClick={handleDecrement}
          style={{
            cursor: disabled || isMinReached ? 'not-allowed' : 'pointer',
            opacity: disabled || isMinReached ? 0.5 : 1,
            userSelect: 'none',
          }}
        />

        <div style={valueContainerStyle} onClick={handleValueClick}>
          {isEditing ? (
            <input
              ref={inputRef}
              type="text"
              value={editValue}
              onChange={handleInputChange}
              onBlur={handleInputBlur}
              onKeyDown={handleInputKeyDown}
              style={inputStyle}
            />
          ) : (
            <Font
              type="heading3"
              fontWeight="semibold"
              color={
                disabled
                  ? colors.primary.coolGray[400]
                  : error
                    ? colors.semantic.state.error
                    : colors.primary.coolGray[800]
              }
              align="center"
              style={{
                userSelect: 'none',
              }}
            >
              {displayValue()}
            </Font>
          )}
        </div>

        <Icon
          type="add-circle-filled"
          size={24}
          color={
            disabled || isMaxReached ? colors.primary.coolGray[400] : colors.primary.coolGray[800]
          }
          onClick={handleIncrement}
          style={{
            cursor: disabled || isMaxReached ? 'not-allowed' : 'pointer',
            opacity: disabled || isMaxReached ? 0.5 : 1,
            userSelect: 'none',
          }}
        />
      </div>

      {error && errorMessage && (
        <div
          style={{
            paddingLeft: '16px',
            width: '100%',
            boxSizing: 'border-box',
            userSelect: 'none',
          }}
        >
          <Font
            type="body2"
            fontWeight="medium"
            color={colors.semantic.state.error}
            align="left"
            style={{
              userSelect: 'none',
            }}
          >
            {errorMessage}
          </Font>
        </div>
      )}
    </div>
  );
};
