import React, { useState, useEffect } from 'react';
import { colors } from '../../tokens/colors';
import { textStyles } from '../../tokens/typography';
import { spacing } from '../../tokens/spacing';
import { radius } from '../../tokens/radius';
import './GreetingHeader.css';

export interface GreetingHeaderProps {
  /**   */
  userName?: string;
  /**   (    ) */
  customGreeting?: string;
  /**    */
  showDate?: boolean;
  /**   */
  weather?: {
    temperature?: number;
    condition?: 'sunny' | 'cloudy' | 'rainy' | 'snowy';
  };
  /**   */
  onClick?: () => void;
  /**  CSS  */
  className?: string;
}

const GreetingHeader: React.FC<GreetingHeaderProps> = ({
  userName = '',
  customGreeting,
  showDate = true,
  weather,
  onClick,
  className = '',
}) => {
  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 60000); // 1 

    return () => clearInterval(timer);
  }, []);

  //   
  const getTimeBasedGreeting = () => {
    const hour = currentTime.getHours();

    if (hour >= 5 && hour < 12) {
      return {
        greeting: ' !',
        emoji: '',
        bgGradient: 'linear-gradient(135deg, #FFE5B4 0%, #FFD700 100%)',
        textColor: colors.primary.gray[800],
      };
    } else if (hour >= 12 && hour < 18) {
      return {
        greeting: ' !',
        emoji: '',
        bgGradient: 'linear-gradient(135deg, #87CEEB 0%, #4FC3F7 100%)',
        textColor: colors.semantic.background.primary,
      };
    } else if (hour >= 18 && hour < 22) {
      return {
        greeting: ' !',
        emoji: '',
        bgGradient: 'linear-gradient(135deg, #FF9A56 0%, #FF6B35 100%)',
        textColor: colors.semantic.background.primary,
      };
    } else {
      return {
        greeting: ' !',
        emoji: '',
        bgGradient: 'linear-gradient(135deg, #2C3E50 0%, #4A6741 100%)',
        textColor: colors.semantic.background.primary,
      };
    }
  };

  //    
  const getWeatherInfo = () => {
    if (!weather || !weather.condition) return null;

    const weatherIcons = {
      sunny: '',
      cloudy: '',
      rainy: '',
      snowy: '',
    };

    return {
      icon: weatherIcons[weather.condition] || '',
      temp: weather.temperature,
    };
  };

  const timeGreeting = getTimeBasedGreeting();
  const weatherInfo = getWeatherInfo();
  const displayGreeting = customGreeting || timeGreeting.greeting;

  //    
  const formatDate = () => {
    const days = ['', '', '', '', '', '', ''];
    const month = currentTime.getMonth() + 1;
    const date = currentTime.getDate();
    const day = days[currentTime.getDay()];

    return `${month} ${date} (${day})`;
  };

  const headerStyles: React.CSSProperties = {
    background: timeGreeting.bgGradient,
    padding: spacing.l,
    borderRadius: radius.l,
    position: 'relative',
    overflow: 'hidden',
    cursor: onClick ? 'pointer' : 'default',
    transition: 'all 0.3s ease',
    minHeight: '120px',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  };

  return (
    <div className={`greeting-header ${className}`} style={headerStyles} onClick={onClick}>
      {/*    */}
      <div
        style={{
          position: 'absolute',
          top: 0,
          right: 0,
          width: '150px',
          height: '150px',
          background: `radial-gradient(circle, rgba(255,255,255,0.1) 1px, transparent 1px)`,
          backgroundSize: '15px 15px',
          opacity: 0.6,
          pointerEvents: 'none',
        }}
      />

      {/*    */}
      <div style={{ position: 'relative', zIndex: 1 }}>
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: spacing.s }}>
          <span style={{ fontSize: '32px', marginRight: spacing.s }}>{timeGreeting.emoji}</span>
          <div>
            <h1
              style={{
                ...textStyles.display1,
                color: timeGreeting.textColor,
                margin: 0,
                marginBottom: spacing.xxxs,
                textShadow: '0px 1px 2px rgba(0,0,0,0.1)',
              }}
            >
              {displayGreeting}
            </h1>
            <p
              style={{
                ...textStyles.heading3,
                color: timeGreeting.textColor,
                margin: 0,
                opacity: 0.9,
              }}
            >
              {userName} 
            </p>
          </div>
        </div>

        {/*    */}
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: spacing.m,
          }}
        >
          {/*   */}
          {showDate && (
            <div style={{ display: 'flex', alignItems: 'center', gap: spacing.xs }}>
              <span style={{ fontSize: '16px' }}></span>
              <span
                style={{
                  ...textStyles.body2,
                  color: timeGreeting.textColor,
                  opacity: 0.8,
                }}
              >
                {formatDate()}
              </span>
            </div>
          )}

          {/*   */}
          {weatherInfo && (
            <div style={{ display: 'flex', alignItems: 'center', gap: spacing.xs }}>
              <span style={{ fontSize: '16px' }}>{weatherInfo.icon}</span>
              {weatherInfo.temp && (
                <span
                  style={{
                    ...textStyles.body2,
                    color: timeGreeting.textColor,
                    opacity: 0.8,
                  }}
                >
                  {weatherInfo.temp}C
                </span>
              )}
            </div>
          )}
        </div>
      </div>

      {/*   () */}
      <div
        style={{
          position: 'absolute',
          bottom: spacing.s,
          right: spacing.m,
          opacity: 0.7,
        }}
      >
        <span
          style={{
            ...textStyles.caption,
            color: timeGreeting.textColor,
            fontStyle: 'italic',
          }}
        >
          {getMotivationalMessage()}
        </span>
      </div>
    </div>
  );
};

//    
const getMotivationalMessage = (): string => {
  const messages = [
    ' ! ',
    '   ',
    '  ! ',
    '   ! ',
    ' ,   ',
    '  ',
    '   ',
  ];

  return messages[Math.floor(Math.random() * messages.length)];
};

export default GreetingHeader;
