import { illustMap, IllustType } from './assets';

export interface IllustProps {
  /**   */
  type: IllustType;
  /**   ( ) */
  size?: number;
  /**    */
  onClick?: () => void;
  /**  CSS  */
  className?: string;
  /**   */
  style?: React.CSSProperties;
}

export const Illust: React.FC<IllustProps> = ({
  type,
  size = 24,
  onClick,
  className = '',
  style = {},
}) => {
  const svgContent = illustMap[type];

  const illustStyle: React.CSSProperties = {
    width: size,
    height: size,
    cursor: 'inherit',
    transition: 'all 0.2s ease',
    display: 'inline-flex',
    alignItems: 'center',
    justifyContent: 'center',
    ...style,
  };

  const handleClick = () => {
    if (onClick) {
      onClick();
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (onClick && (e.key === 'Enter' || e.key === ' ')) {
      e.preventDefault();
      onClick();
    }
  };

  if (!svgContent) {
    console.warn(`Illust type "${type}" not found`);
    return (
      <div
        className={`illust illust--${type} ${className}`}
        style={illustStyle}
        onClick={handleClick}
        role={onClick ? 'button' : 'img'}
        tabIndex={onClick ? 0 : undefined}
        onKeyDown={handleKeyDown}
      >
        <svg width={size} height={size} viewBox="0 0 24 24" fill="none">
          <circle cx="12" cy="12" r="1" fill="currentColor" />
        </svg>
      </div>
    );
  }

  // SVG     (color  )
  const processedSvg = svgContent
    .replace(/width="[^"]*"/g, `width="${size}"`)
    .replace(/height="[^"]*"/g, `height="${size}"`);

  return (
    <div
      className={`illust illust--${type} ${className}`}
      style={illustStyle}
      onClick={handleClick}
      role={onClick ? 'button' : 'img'}
      tabIndex={onClick ? 0 : undefined}
      onKeyDown={handleKeyDown}
      dangerouslySetInnerHTML={{ __html: processedSvg }}
    />
  );
};
