import React, { useState, useMemo } from 'react';
import { colors } from '../../tokens/colors';
import { textStyles } from '../../tokens/typography';
import { spacing } from '../../tokens/spacing';
import { radius } from '../../tokens/radius';
import ExerciseCard, { ExerciseCardProps } from '../exercise-card/ExerciseCard';
import './ExerciseList.css';

export interface Exercise
  extends Omit<ExerciseCardProps, 'onClick' | 'onFavoriteToggle' | 'onCompleteToggle'> {
  id: string;
}

export interface ExerciseListProps {
  /**   */
  exercises: Exercise[];
  /**    */
  emptyMessage?: string;
  /**    */
  emptyIcon?: React.ReactNode;
  /**     */
  showFilters?: boolean;
  /**     */
  showSearch?: boolean;
  /**    */
  onExerciseClick?: (exercise: Exercise) => void;
  /**    */
  onFavoriteToggle?: (exerciseId: string) => void;
  /**    */
  onCompleteToggle?: (exerciseId: string) => void;
  /**  CSS  */
  className?: string;
}

type FilterType = 'all' | 'cardio' | 'strength' | 'stretching' | 'balance';
type SortType = 'name' | 'duration' | 'calories' | 'type';

const ExerciseList: React.FC<ExerciseListProps> = ({
  exercises,
  emptyMessage = '  .',
  emptyIcon = '',
  showFilters = true,
  showSearch = true,
  onExerciseClick,
  onFavoriteToggle,
  onCompleteToggle,
  className = '',
}) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [filterType, setFilterType] = useState<FilterType>('all');
  const [sortBy, setSortBy] = useState<SortType>('name');
  const [showCompleted, setShowCompleted] = useState(true);

  //     
  const filteredAndSortedExercises = useMemo(() => {
    let filtered = exercises;

    //  
    if (searchQuery.trim()) {
      filtered = filtered.filter(
        (exercise) =>
          exercise.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
          (exercise.description &&
            exercise.description.toLowerCase().includes(searchQuery.toLowerCase())),
      );
    }

    //  
    if (filterType !== 'all') {
      filtered = filtered.filter((exercise) => exercise.type === filterType);
    }

    //   
    if (!showCompleted) {
      filtered = filtered.filter((exercise) => !exercise.isCompleted);
    }

    // 
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.title.localeCompare(b.title);
        case 'duration':
          return (b.duration || 0) - (a.duration || 0);
        case 'calories':
          return (b.calories || 0) - (a.calories || 0);
        case 'type':
          return (a.type || '').localeCompare(b.type || '');
        default:
          return 0;
      }
    });

    return filtered;
  }, [exercises, searchQuery, filterType, sortBy, showCompleted]);

  //  
  const stats = useMemo(() => {
    const completed = exercises.filter((ex) => ex.isCompleted).length;
    const total = exercises.length;
    const totalDuration = exercises.reduce((sum, ex) => sum + (ex.duration || 0), 0);
    const totalCalories = exercises.reduce((sum, ex) => sum + (ex.calories || 0), 0);

    return {
      completed,
      total,
      completionRate: total > 0 ? Math.round((completed / total) * 100) : 0,
      totalDuration,
      totalCalories,
    };
  }, [exercises]);

  const filterOptions = [
    { value: 'all' as FilterType, label: '', icon: '' },
    { value: 'cardio' as FilterType, label: '', icon: '' },
    { value: 'strength' as FilterType, label: '', icon: '' },
    { value: 'stretching' as FilterType, label: '', icon: '' },
    { value: 'balance' as FilterType, label: '', icon: '' },
  ];

  const sortOptions = [
    { value: 'name' as SortType, label: '' },
    { value: 'duration' as SortType, label: '' },
    { value: 'calories' as SortType, label: '' },
    { value: 'type' as SortType, label: '' },
  ];

  return (
    <div className={`exercise-list ${className}`}>
      {/*   */}
      <div style={{ marginBottom: spacing.l }}>
        {/*   */}
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: spacing.m,
            padding: spacing.m,
            backgroundColor: colors.primary.coolGray[50],
            borderRadius: radius.s,
            border: `1px solid ${colors.semantic.border.default}`,
          }}
        >
          <div>
            <h2
              style={{
                ...textStyles.heading2,
                color: colors.semantic.text.primary,
                margin: 0,
                marginBottom: spacing.xxxs,
              }}
            >
                
            </h2>
            <p
              style={{
                ...textStyles.body3,
                color: colors.semantic.text.secondary,
                margin: 0,
              }}
            >
               {stats.total}   {stats.completionRate}%
            </p>
          </div>
          <div style={{ display: 'flex', gap: spacing.s, alignItems: 'center', fontSize: '12px' }}>
            {stats.totalDuration > 0 && (
              <div style={{ textAlign: 'center', color: colors.semantic.text.tertiary }}>
                <div> {stats.totalDuration}</div>
              </div>
            )}
            {stats.totalCalories > 0 && (
              <div style={{ textAlign: 'center', color: colors.semantic.text.tertiary }}>
                <div> {stats.totalCalories}kcal</div>
              </div>
            )}
          </div>
        </div>

        {/*    */}
        {(showSearch || showFilters) && (
          <div style={{ marginBottom: spacing.m }}>
            {/*   */}
            {showSearch && (
              <div style={{ marginBottom: spacing.s }}>
                <input
                  type="text"
                  placeholder=" ..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  style={{
                    width: '100%',
                    padding: `${spacing.s} ${spacing.m}`,
                    border: `1px solid ${colors.semantic.border.default}`,
                    borderRadius: radius.s,
                    fontSize: '14px',
                    backgroundColor: colors.semantic.background.primary,
                    color: colors.semantic.text.primary,
                    outline: 'none',
                    transition: 'border-color 0.2s ease',
                  }}
                  onFocus={(e) => {
                    e.target.style.borderColor = colors.primary.mainviolet;
                  }}
                  onBlur={(e) => {
                    e.target.style.borderColor = colors.semantic.border.default;
                  }}
                />
              </div>
            )}

            {/*    */}
            {showFilters && (
              <div
                style={{ display: 'flex', gap: spacing.s, flexWrap: 'wrap', alignItems: 'center' }}
              >
                {/*   */}
                <div style={{ display: 'flex', gap: spacing.xxs }}>
                  {filterOptions.map((option) => (
                    <button
                      key={option.value}
                      onClick={() => setFilterType(option.value)}
                      style={{
                        padding: `${spacing.xs} ${spacing.s}`,
                        border: `1px solid ${
                          filterType === option.value
                            ? colors.primary.mainviolet
                            : colors.semantic.border.default
                        }`,
                        borderRadius: radius.xs,
                        backgroundColor:
                          filterType === option.value
                            ? colors.primary.mainviolet
                            : colors.semantic.background.primary,
                        color:
                          filterType === option.value
                            ? colors.semantic.background.primary
                            : colors.semantic.text.primary,
                        cursor: 'pointer',
                        fontSize: '12px',
                        display: 'flex',
                        alignItems: 'center',
                        gap: spacing.xxxs,
                        transition: 'all 0.2s ease',
                      }}
                    >
                      <span>{option.icon}</span>
                      {option.label}
                    </button>
                  ))}
                </div>

                {/*  */}
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value as SortType)}
                  style={{
                    padding: `${spacing.xs} ${spacing.s}`,
                    border: `1px solid ${colors.semantic.border.default}`,
                    borderRadius: radius.xs,
                    backgroundColor: colors.semantic.background.primary,
                    color: colors.semantic.text.primary,
                    fontSize: '12px',
                    cursor: 'pointer',
                  }}
                >
                  {sortOptions.map((option) => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </select>

                {/*     */}
                <label
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    gap: spacing.xs,
                    cursor: 'pointer',
                    fontSize: '12px',
                    color: colors.semantic.text.secondary,
                  }}
                >
                  <input
                    type="checkbox"
                    checked={showCompleted}
                    onChange={(e) => setShowCompleted(e.target.checked)}
                    style={{ cursor: 'pointer' }}
                  />
                    
                </label>
              </div>
            )}
          </div>
        )}
      </div>

      {/*   */}
      {filteredAndSortedExercises.length > 0 ? (
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            gap: spacing.s,
          }}
        >
          {filteredAndSortedExercises.map((exercise) => (
            <ExerciseCard
              key={exercise.id}
              {...exercise}
              width="fill"
              onClick={() => onExerciseClick?.(exercise)}
              onFavoriteToggle={() => onFavoriteToggle?.(exercise.id)}
              onCompleteToggle={() => onCompleteToggle?.(exercise.id)}
            />
          ))}
        </div>
      ) : (
        /*   */
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            padding: `${spacing.xxxl} ${spacing.l}`,
            textAlign: 'center',
            color: colors.semantic.text.tertiary,
          }}
        >
          <div style={{ fontSize: '48px', marginBottom: spacing.m }}>{emptyIcon}</div>
          <h3
            style={{
              ...textStyles.heading3,
              color: colors.semantic.text.secondary,
              margin: 0,
              marginBottom: spacing.xs,
            }}
          >
            {emptyMessage}
          </h3>
          <p
            style={{
              ...textStyles.body3,
              color: colors.semantic.text.tertiary,
              margin: 0,
              maxWidth: '300px',
            }}
          >
            {searchQuery || filterType !== 'all'
              ? '  .'
              : '  !'}
          </p>
        </div>
      )}
    </div>
  );
};

export default ExerciseList;
