import React, { ReactNode, useCallback } from 'react';
import toast, { Toaster } from 'react-hot-toast';
import { Toast, ToastProps } from './Toast';

// Toast   (  )
export interface ToastItem extends Omit<ToastProps, 'onClose'> {
  id?: string;
  duration?: number;
}

// Provider Props
interface ToastProviderProps {
  children: ReactNode;
  /** Toast   */
  position?:
    | 'top-right'
    | 'top-left'
    | 'bottom-right'
    | 'bottom-left'
    | 'top-center'
    | 'bottom-center';
  /**    (ms) */
  defaultDuration?: number;
}

/**
 * Toast Provider 
 *
 * react-hot-toast   Toast      Provider.
 */
export const ToastProvider: React.FC<ToastProviderProps> = ({
  children,
  position = 'top-right',
  defaultDuration = 4000,
}) => {
  return (
    <>
      {children}
      <Toaster
        position={position}
        toastOptions={{
          duration: defaultDuration,
          style: {
            background: 'transparent',
            boxShadow: 'none',
            padding: 0,
            margin: 0,
          },
        }}
      />
    </>
  );
};

/**
 * useToast 
 *
 * react-hot-toast   API   Toast    .
 */
export const useToast = () => {
  // dismiss      
  const dismissToast = useCallback((id: string) => toast.dismiss(id), []);

  const success = useCallback(
    (title: string, description?: string, options?: Partial<ToastItem>) => {
      return toast.custom(
        (t) => (
          <Toast
            key={t.id}
            status="success"
            title={title}
            description={description}
            showLeadingIcon={options?.showLeadingIcon ?? true}
            showCloseButton={true}
            onClose={() => dismissToast(t.id)}
            {...options}
          />
        ),
        {
          duration: options?.duration || 4000,
          id: options?.id,
        },
      );
    },
    [dismissToast],
  );

  const error = useCallback(
    (title: string, description?: string, options?: Partial<ToastItem>) => {
      return toast.custom(
        (t) => (
          <Toast
            key={t.id}
            status="error"
            title={title}
            description={description}
            showLeadingIcon={options?.showLeadingIcon ?? true}
            showCloseButton={true}
            onClose={() => dismissToast(t.id)}
            {...options}
          />
        ),
        {
          duration: options?.duration || 4000,
          id: options?.id,
        },
      );
    },
    [dismissToast],
  );

  const warning = useCallback(
    (title: string, description?: string, options?: Partial<ToastItem>) => {
      return toast.custom(
        (t) => (
          <Toast
            key={t.id}
            status="warning"
            title={title}
            description={description}
            showLeadingIcon={options?.showLeadingIcon ?? true}
            showCloseButton={true}
            onClose={() => dismissToast(t.id)}
            {...options}
          />
        ),
        {
          duration: options?.duration || 4000,
          id: options?.id,
        },
      );
    },
    [dismissToast],
  );

  const info = useCallback(
    (title: string, description?: string, options?: Partial<ToastItem>) => {
      return toast.custom(
        (t) => (
          <Toast
            key={t.id}
            status="info"
            title={title}
            description={description}
            showLeadingIcon={options?.showLeadingIcon ?? true}
            showCloseButton={true}
            onClose={() => dismissToast(t.id)}
            {...options}
          />
        ),
        {
          duration: options?.duration || 4000,
          id: options?.id,
        },
      );
    },
    [dismissToast],
  );

  const custom = useCallback(
    (options: Omit<ToastItem, 'id'>) => {
      return toast.custom(
        (t) => (
          <Toast
            key={t.id}
            showLeadingIcon={true}
            showCloseButton={true}
            onClose={() => dismissToast(t.id)}
            {...options}
          />
        ),
        {
          duration: options.duration || 4000,
        },
      );
    },
    [dismissToast],
  );

  const remove = useCallback((id: string) => dismissToast(id), [dismissToast]);
  const removeAll = useCallback(() => toast.dismiss(), []);

  return {
    success,
    error,
    warning,
    info,
    custom,
    remove,
    removeAll,
  };
};

export default ToastProvider;
